.App {
  text-align: center;
  width: 100%;
  height: 100vh;

}

.pokemonCardContainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  margin-top: 20px;

}

.btn {
  padding: 30px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.btn button {
  padding: 13px 32px;
  background-color: rgb(255, 187, 127);
  border: none;
  box-shadow: 2px 8px 21px -2px;
  border-radius: 10px;
  cursor: pointer;
  color: white;
  font-weight: bold;
  transition: all 0.3s;
}

.btn button:hover {
  transform: translateY(5px);
  box-shadow: none;
}

.searchBox {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 10px;
  padding: 10px;
 
}

.doSearch {
  display: flex;
  justify-content:center;

}
